<template>
    <div class="footer_wrap">
        <div class="left fl">
            <h1 class="mb15 logo">2KEX Global</h1>
            <p class="mb15"></p>
            <div class="icon_img">
                <i class="iconfont icon-weibo"></i>
                <i class="iconfont icon-weibo"></i>
                <i class="iconfont icon-weibo"></i>
                <i class="iconfont icon-weibo"></i>
            </div>
            <p class="mt50"> 2013-2018 2KEX Global</p>
        </div>
        <div class="fr">
            <ul>
                <li class="pb50">
                    <p class="fl tl foot_title ft12" v-for="item in titleList">{{item}}</p>
                </li>
                <li v-for="item in conList"><a class="con_a mb15 ft16" v-for="itm in item.list" v-bind:href="itm.src">{{itm.text}}</a></li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
  data() {
    return {
      titleList: ["", "", "", ""],
      conList: [
        {
          list: [{ text: "2KEX", src: "" },{ text: "", src: "" },{ text: "2KEX", src: "" },{ text: "", src: "" }]
        },
        {
          list: [{ text: "2KEX", src: "" },{ text: "", src: "" },{ text: "", src: "" },{ text: "", src: "" }]
        },
        {
          list: [{ text: "2KEX", src: "" },{ text: "API", src: "" },{ text: "", src: "" },{ text: "", src: "" }]
        },
        {
          list: [{ text: "2KEX", src: "" },{ text: "", src: "" },{ text: "", src: "" },{ text: "", src: "" }]
        },
        {
          list: [{ text: "", src: "" },{ text: "", src: "" },{ text: "", src: "" }]
        }
      ]
    };
  }
};
</script>
<style scoped>
.footer_wrap {
  background: #060e20;
  color: #6b80ae;
  overflow: hidden;
  padding: 50px 80px;
}
.foot_title,.con_a{
  display: inline-block;
  width: 180px;
  text-align: left;
}
.con_a{
    color: #c7cce6;
}
.con_a:hover{
    color: #6b80ae;
}
.logo{
    color: #6b80ae;
}
</style>





